<template>
  <div class="textCard">
    <div
      class="con"
    >{{text.desc}}</div>

    <div class="tab" v-if="[3,4,5,6].includes(gradeInfo.id) && $route.meta[0]==='1'">
      <span
        v-for="itm in tab"
        :key="itm.id"
        :class="{tabItem:true,active:flag === itm.id}"
        @touchstart="tabItemTouchStart(itm)"
      >{{itm.name}}</span>
    </div>
   
  </div>
</template>

<script>
export default {
  name: "TextCard",
  components: {},
  props: {
    text: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      tab: [
        {
          id: 1,
          name: "学生篇"
        },
        {
          id: 2,
          name: "家长篇"
        }
      ],
      flag: 1,
      gradeInfo:{}
    };
  },
  created() {
      this.gradeInfo = JSON.parse(localStorage.getItem('gradeInfo'))||{}   
  },
  mounted() {},
  methods: {
    tabItemTouchStart(itm) {
      this.flag = itm.id;
      this.$emit('tabChange',{
          val:itm
      })
    }
  }
};
</script>


<style scoped lang='less'>
.textCard {
  margin: 0.3rem 0;
  .con {
    width: 100%;
  }
  .tab {
    width: 100%;
    margin: .16rem 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    .tabItem {
      margin-right: 0.3rem;
      &.active {
        color: #ff8017;
      }
    }
  }
}
</style>
