<template>
  <div class="compositionBase">
    <TextCard @tabChange="tabChange" :text="initData.TextCard" />
    <ContentBar :tabInfo="tabInfo" :content="initData.content" :bottomTextData="initData.bottomText"/>
  </div>
</template>

<script>
import TextCard from "@/components/common/TextCard";
import ContentBar from "@/components/common/ContentBar";


import grade from "@/assets/js/data";

export default {
  name: "CompositionBase",
  components: {
    TextCard,
    ContentBar
  },
  props: {},
  data() {
    return {
      tabInfo: {
        val: {
          id: 1,
          name: "学生篇"
        }
      },
      initData: {
        content: {}
      }
    };
  },
  created() {
    this.setData(); 
  },
  methods: {
    tabChange(data) {
      this.tabInfo = data || {};
    },
    setData() {
      this.initData =
        grade["grade" + JSON.parse(localStorage.getItem("gradeInfo")).id]["0"];
    }
  },
  beforeRouteEnter(to, from, next) {
    if (!localStorage.getItem("gradeInfo")) {
      next("/");
    } else {
      next();
    }
  }
};
</script>


<style scoped>
.compositionBase {
  margin: 0 3% 0.4rem;
}
</style>
