<template>
  <div class="contentBar">
    <ul class="tabCon" v-if="[3,4,5,6].includes(gradeInfo.id) && $route.meta[0]==='1'">
      <li class="tabConItem" v-show="tab.val.id==1">
        <ContentItem v-for="(itm,idx) in content['0']" :key="idx" :content="itm" />
      </li>
      <li class="tabConItem" v-show="tab.val.id==2">
        <ContentItem v-for="(itm,idx) in content['1']" :key="idx" :content="itm" />
        <BottomText v-if="bottomTextData&&bottomTextData.desc" :bottomText="bottomTextData" />
      </li>
    </ul>
    <div class="conBox" v-else>
      <ContentItem v-for="(itm,idx) in content['0']" :key="idx" :content="itm" />
      <BottomText v-if="bottomTextData&&bottomTextData.desc" :bottomText="bottomTextData" />
    </div>
  </div>
</template>

<script>
import ContentItem from "@/components/common/ContentItem";
import BottomText from "@/components/common/BottomText";
export default {
  name: "ContentBar",
  components: {
    ContentItem,
    BottomText
  },
  props: {
    tabInfo: {
      type: Object,
      default() {
        return {
          val: {
            id: 1,
            name: "学生篇"
          }
        };
      }
    },
    content: {
      type: Object,
      default() {
        return {};
      }
    },
    bottomTextData: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {};
  },
  computed: {
    tab() {
      return this.tabInfo;
    }
  },
  created() {
    this.gradeInfo = JSON.parse(localStorage.getItem("gradeInfo"));
  }
};
</script>

<style scoped lang="less">
.contentBar {
}
</style>
